
import { Link } from "react-router-dom";
//Images
import vectorImg from "../assets/images/backgrounds/resume-media.svg";

// CSS
import '../assets/css/pages/resume.css';

function Resume() {
    return (
        <>
            <div className="main-content">
                <section className="resume-welcome">
                    <div className="container">
                        <div className="resume-bg">
                            <div className="resume-media text-center">
                                <img src={vectorImg} alt="" />
                            </div>
                            <div className="resume-welcome-text">
                                <div className="section-title text-center">
                                    <h4>How would you like to <br/> Get Started?</h4>
                                    <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Purus neque, tellus morbi turpis erat cursus neque felis. In arcu libero sed tempor mauris purus at.</p>
                                </div>
                                <button className="button button-blue mb-4">Upload Your CV</button>
                                <Link className="button button-light-blue" to="/createResume/PersonalInformation">Fill in Manually</Link>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        </>
    )
}

export default Resume;